header{
    height: 100vh;
    padding-top: 7rem;
   
    overflow: hidden;


}

.header__container{
    text-align: center;
    height: 100%;
    position: relative;
    margin-top: 3rem;
 
}

/* CTA */

.cta{
    margin-top: 2.5rem;
    display: flex;
    gap : 1.2rem;
    justify-content: center;
    position: relative;

}

/* Socials */

.header__socials{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
    position: absolute;
    left: 0;
    bottom: 3rem;
}

.header__socials::after{
    content: "";
    width: 1px;
    height: 2rem;
    background: var(--color-primary);
}

/* IMAGE */

.me{
    background: linear-gradient(var(--color-primary), transparent);
    width: 22rem;
    height: 30rem;
    position: absolute;
    left: calc(50% - 11rem);
    margin-top: 4rem;
    border-radius: 12rem 12rem 0 0 ;
    overflow: hidden;
    padding: 5rem 1.5rem 1.5rem;
    -webkit-animation: mover 1.5s infinite  alternate;
    animation: mover 1.5s infinite  alternate;

}


@-webkit-keyframes mover {
    0% { transform: translateY(0); }
    100% { transform: translateY(-20px); }
}
@keyframes mover {
    0% { transform: translateY(0); }
    100% { transform: translateY(-20px); }
}

.small-flex{
    margin: 2rem 0.2 2.6rem;
    color: var(--color-light);
    font-size: 0.8rem;
}

/* scroll */

.scroll__down{
    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;
}

@media screen and (max-width: 1024px){
    header{
        height: 100vh;

    }
}

@media screen and (max-width: 600px){
    header{
        height: 100vh;
        /* grid-template-columns: 1fr; */
    }
    .header__socials, 
        .scroll__down{
            display: none;
        }
       
    }
