@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap');
*{
    margin:0;
    padding: 0;
    border: 0;
    outline: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
  

}

/*Initialising variables to be used throughout  */
:root{
    --color-bg: #1f1f38;
    --color-bg-variant: #2c2c6c;
    --color-primary: #4db5ff;
    --color-primary-variant: rgba(77,181,255,0.4);
    --color-white: #fff;
    --color-light: rgba(255,255,255,0.6);
    --container-width-lg: 75%;
    --container-width-md: 86%;
    --container-width-sm: 90%;
    --transition: all 400ms ease;
}
.light-theme {
    --color-primary-variant: rgb(161, 0, 53);
    --color-bg: rgb(255, 231, 191);
    --color-bg: #f4c2c2;
    --color-bg-variant: rgb(252, 176, 176);
    --color-primary: rgb(161, 0, 53);

    --color-white: black;
    --color-light: rgb(161, 0, 53);
    background:#ffe9ec;
    color:#ff6a80;
   
    background:#ffe9ec;
    background-image: url(../src/assets/brick-wall.png);
 

}
  
  .dark-theme {
    --color-bg: #1f1f38;
    --color-bg-variant: #2c2c6c;
    --color-primary: #4db5ff;
    --color-primary-variant: rgba(77,181,255,0.4);
    --color-white: #fff;
    --color-light: rgba(255,255,255,0.6);
  }
html{
    scroll-behavior: smooth;
}

/* This is to not have a scrollbar */
::-webkit-scrollbar{

    display: none;
}
body{
    font-family: 'Poppins', sans-serif;
    background: var(--color-bg);
    color: var(--color-white);
    line-height: 1.7;
    

    background-image: url(../src/assets/bg-texture.png);
    
}

/* general styles */

.container{
    width: var(--container-width-lg);
    margin: 0 auto;
}

h1,h2,h3,h4,h5{
    font-weight: 500;
}

h1{
    font-size: 2.5rem;
}
section{
    margin-top: 8rem;
    
    

}

section{
    color: var(--color-primary);
   
}

section > h2,
section >h5{
    text-align: center;
    color: var(--color-light)
}

section > h2{
    color: var(--color-primary);
    margin-bottom: 3rem;
}

.text-light{
    color: var(--color-light);
}

a{
    color: var(--color-primary);
    transition: var(--transition);
}
a:hover{
    color: var(--color-white)

}


.btn{
    width: max-content;
    display: inline-block;
    color: var(--color-primary);
    padding: 0.75rem 1.2rem;
    border-radius: 0.4rem;
    cursor: pointer;
    border: 1px solid var(--color-primary);
    transition: var(--transition);
}

.btn:hover{
    background: var(--color-white);
    color: var(--color-bg);
    border-color: transparent;
}

.btn-primary{
    background: var(--color-primary);
    color: var(--color-bg);
}
.img{
    display: block;
    width: 100%;
    object-fit: cover;
    
   
}

.font-link {
    font-family: 'Poppins', sans-serif;
    border-radius: 0.4rem;
    cursor: pointer;
    font-weight: 500;
    font-size: 0.8rem;
    text-align: center;


    /* border: 1px solid var(--color-primary);  */
  }
  
